<template>
  <div id="dashboard">
    <div class="section">
      <div v-if="collection !== 'none'" class="fixed-action-btn">
        <router-link to="/new" class="btn-floating btn-large red">
        <i class="fa fa-plus"></i>
        </router-link>
      </div>
      <div class="totals-grid valign-wrapper center">
        <div class="grid-item-tot"><span class="flow-text center-align">BUS: {{busTotal}}</span></div>
        <div class="grid-item-tot"><span class="flow-text center-align">VAN: {{vanTotal}}</span></div>
        <div class="grid-item-tot"><span class="flow-text center-align">CLASS: {{classTotal}}</span></div>
        </div>
        <div class="" v-for="kid in this.kidz" v-bind:key="kid.id">
          <div class="dash-grid">
          <div class="chip grid-item-nobo "><span class="flow-text">{{kid.age}}</span></div>
          <div class="grid-item-nobo">
            <router-link class="" v-bind:to="{name: 'view-employee', params: {kid_id: kid.id, colName: collection}}">
              <div class="" ><span class="flow-text kid-name">{{kid.name}}</span></div>
            </router-link>
          </div>
          <div v-on:click="bus_checkInOut(kid.id, kid.bus)" v-bind:class="[{checkedin: kid.bus}, 'grid-item']"><span class="flow-text">Bus</span></div>
          <div v-on:click="van_checkInOut(kid.id, kid.van)" v-bind:class="[{checkedin: kid.van}, 'grid-item']"><span class="flow-text">Van</span></div>
          <div v-on:click="class_checkInOut(kid.id, kid.class)" v-bind:class="[{checkedin: kid.class}, 'grid-item']"><span class="flow-text">Class</span></div>
    </div>
    <div class="fix-action-btn"></div>
    </div>
    </div>
  </div>
</template>

<script>
import db from "./firebaseInit.js";
import firebase from "firebase";
export default {
  name: "dashboard",
  data() {
    return {
      kidz: [],
      collection: this.$store.state.colName
    }
  },

  created() {
    db.collection(this.$store.state.colName)
      .orderBy("name")
      .onSnapshot(snap => {
        this.kidz = []
        var _this = this
        snap.forEach(doc => {
          const data = {
            id: doc.id,
            name: doc.data().name,
            in_out: doc.data().in_out,
            age: doc.data().age,
            phone: doc.data().phone,
            bus: doc.data().bus,
            van: doc.data().van,
            class: doc.data().class
          }
          _this.kidz.push(data)
        })
      })
      this.$store.watch(
        (state) => {
      return state.colName
    },
    (val) => {
      db.collection(this.$store.state.colName)
      .orderBy("name")
      .onSnapshot(snap => {
        this.kidz = []
        this.busTotal = 0
        var _this = this
        snap.forEach(doc => {
          const data = {
            id: doc.id,
            name: doc.data().name,
            in_out: doc.data().in_out,
            age: doc.data().age,
            phone: doc.data().phone,
            bus: doc.data().bus,
            van: doc.data().van,
            class: doc.data().class

          }
          _this.kidz.push(data)
        })
        _this.collection = this.$store.state.colName
      })
    },
    {
      deep: true
    }
  )

  },
  methods: {
    bus_checkInOut(id, inout) {
      db.collection(this.$store.state.colName)
        .doc(id)
        .update({ bus: !inout })
        .then(docRef => {
        });
    },
    van_checkInOut(id, inout) {
      db.collection(this.$store.state.colName)
        .doc(id)
        .update({ van: !inout })
        .then(docRef => {
        });
    },
    class_checkInOut(id, inout) {
      db.collection(this.$store.state.colName)
        .doc(id)
        .update({ class: !inout })
        .then(docRef => {
        });
    },
  },
  computed: {
    busTotal () {
      var total = this.kidz.filter(function (item) {
        return item.bus == true;
        })
        return total.length;
    },
    vanTotal () {
      var total = this.kidz.filter(function (item) {
        return item.van == true;
        })
        return total.length;
    },
    classTotal () {
      var total = this.kidz.filter(function (item) {
        return item.class == true;
        })
        return total.length;
    }

  }
};
</script>

<style scoped>
#dashboard {
  /* max-width: 600px;                                 
  margin: 0 auto; */
}

.checkedin {
  background-color: lightgreen;
  color: gray;
}

.checkedout {
  background-color: lightcoral
}

.kid-name {
  /* font-size: 18px; */
  font-weight: bold;
  /* margin-left: 1rem; */
}

.phone {
  font-size: 12px;
  font-weight: 500;
  font-style: italic;
  margin: auto;
}

.b-border {
  border-bottom: 1px solid darkolivegreen;
}

.row {
  margin-top: 5px;
  padding: 5px;
  background-color: lightgoldenrodyellow;
}

.dash-grid {
  display: grid;
  grid-column-gap: 5px;
  grid-row-gap: 20px;
  grid-template-columns: 1fr 3fr 1fr 1fr 1fr;
  padding: 5px;
  /* background-color: aliceblue; */
}
.totals-grid {
  display: grid;
  grid-column-gap: 5px;
  grid-row-gap: 5px;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 5px;
  margin-bottom: 20px;
}

.grid-item {
  padding: 5px;
  text-align: center;
  border: 1px solid rgba(0, 0, 0, 0.5);
  box-shadow: 0px 0px 2px 0px rgb(79, 207, 90);
  border-radius: 5px;
}
.grid-item-tot {
  padding: 5px;
  text-align: center;
  font-weight: bold;
  border: 1px solid rgba(0, 0, 0, 0.5);
  background-color: rgb(212, 255, 148)
}

.grid-item-nobo {
  margin: auto auto auto 0;
  text-align: center;
}

/*li.collection.item{
  padding: 10px 20px !important;
}*/
</style>



